.header {
	background-color: rgb(0, 224, 224);
    position: sticky;
	width: auto;
	display: flex;
	align-items: center;
	/* position: relative; */
	height: 70px;
	padding: 0px 0px 0px 10px;
}
.switch {
	left: 1200px;
	position: absolute;
	display: inline-block;
	width: 60px;
	height: 34px;
}

.switch input {
	display: none;
}
.slider {
	position: absolute;
	cursor: pointer;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	background-color: #ccc;
	-webkit-transition: 0.4s;
	transition: 0.4s;
}

.slider:before {
	position: absolute;
	content: '';
	height: 26px;
	width: 26px;
	left: 4px;
	bottom: 4px;
	background-color: white;
	-webkit-transition: 0.4s;
	transition: 0.4s;
}

input:checked + .slider {
	background-color: #21394d;
}

input:focus + .slider {
	box-shadow: 0 0 1px #21394d;
}

input:checked + .slider:before {
	-webkit-transform: translateX(26px);
	-ms-transform: translateX(26px);
	transform: translateX(26px);
}

.slider {
	border-radius: 34px;
}

.slider:before {
	border-radius: 50%;
}
